import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController, ToastController, AlertCmp, AlertController } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';
@IonicPage({
  name: 'page-my-security',
  segment: 'my-security'
})

@Component({
  selector: 'page-my-security',
  templateUrl: 'my-security.html'
})

export class MySecurityPage {

  data: any;
  enable: boolean = false;
  disable: boolean = false;

  constructor(public navCtrl: NavController, public translate: TranslateService, public forgotCtrl: AlertController, public user: UserProvider, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {

    this.translate.setDefaultLang('en');
    this.translate.use('es');
    
    // this.userinfo();
    this.checkAllData();

    console.log(JSON.parse(localStorage.getItem('2fa')));
    if (JSON.parse(localStorage.getItem('2fa')) === 0) {
      this.enable = true;
      this.disable = false;
    } else if (JSON.parse(localStorage.getItem('2fa')) === 1) {
      this.enable = false;
      this.disable = true;
    }



  }

  checkAllData() {
    this.user.userInfo().subscribe(
      data => {
        let datas = JSON.parse(JSON.stringify(data));
        let data2 = JSON.parse(datas._body);
        
        if (data2.success == false) {
          // let toast = this.toastCtrl.create({
          // 	showCloseButton: true,
          // 	cssClass: 'profiles-bg',
          // 	message: 'Your session expired due to an extended period of inactivity. Please log in again.',
          // 	duration: 3000,
          // 	position: 'top'
          // });

          // toast.onDidDismiss(() => {
          console.log('Dismissed toast');
          localStorage.removeItem('currentUser');
          localStorage.removeItem('MemberId');
          localStorage.removeItem("token");
          localStorage.removeItem('chekout');
          localStorage.removeItem('2fa');
          localStorage.removeItem('VerificationLevel');
          this.navCtrl.setRoot('page-auth');
          // });

          // toast.present();

        } else {



        }

      }


    )
  }
  //  go to changepassword page
  changePassword() {

    this.navCtrl.push('page-change-password');

  }
  //  go to 2FA enabel page
  twofactoAuth() {


    this.navCtrl.push('page-2FA-0');

  }
  //  go to 2FA Disbale page
  twofactoAuth1() {

    this.navCtrl.push('page-2FA-1');

  }



}



